<template>
  <div class="share-online-activity full-screen">
    <div class="page-panel">
      <!-- <pem-scroll-view class="scroll-view" ref="scroll"> -->
        <div class="container">
          <div class="wrapper">
            <ul class="m-ul">
              <li class="m-li m-li-name">
                <div class="title">会议名称：</div>
                <div class="desc">{{ name }}</div>
              </li>
              <li class="m-li">
                <div class="title">会议介绍：</div>
                <div class="desc desc-intro">{{ introduce }}</div>
              </li>
            </ul>
            <div class="join-btn" @click="joinMeeting">加入线上路演会议</div>
          </div>
        </div>
      <!-- </pem-scroll-view> -->
      <div class="guide-open-browser" v-if="showGuideMask" @click="closeMask">
        <div class="guide-img"></div>
      </div>
    </div>
    <pem-toast ref="toast" :text="toastMsg"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
// import PemScrollView from 'components/scroll-view/scroll-view'
import PemToast from 'components/pem-toast/pem-toast'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle, device } from 'common/js/util'

export default {
  data () {
    return {
      rootImgUrl: ImgPath,
      isWeixin: device.isWeixin(),
      isIOS: device.isIOS(),
      showGuideMask: false,
      toastMsg: '',
      apkUrl: '',
      id: +this.$route.query.id || null,
      name: this.$route.query.name || '',
      introduce: '',
      logo: 'http://cdn.xcx.pemarket.com.cn/img_logo_44x44_default.png'
    }
  },
  components: {
    // PemScrollView,
    PemToast
  },
  created () {
    this._initData()
  },
  methods: {
    _initData () {
      if (this.$route.query.logo) {
        this.logo = this.rootImgUrl + this.$route.query.logo
      }
      setTitle(this.name || '项目工场')
      this.wxShare({
        title: `邀您参加【${this.name}】线上路演活动`,
        content: '亲，打开了解活动详情',
        imgUrl: this.logo
      })
      this.getAndroidApkUrl()
      this._getActivityMettingById({id: this.id})
    },
    joinMeeting () {
      if (this.isIOS) {
        window.location.href = `https://go.pemarket.com.cn/download-guide?type=8&id=${this.$route.query.id}`
      } else {
        if (this.isWeixin) {
          this.showGuideMask = true
        } else {
          window.location.href = `pemarket://www.pemarket.com.cn/attr?type=8&id=${this.$route.query.id}`
          setTimeout(() => {
            window.location.href = this.apkUrl
          }, 2000)
        }
      }
    },
    closeMask () {
      this.showGuideMask = false
    },
    showToast (msg) {
      this.toastMsg = msg
      this.$refs.toast.show()
    },
    _getActivityMettingById (options) {
      MiniSiteApi.getActivityMettingById(options).then(data => {
        if (data.name) { this.name = data.name }
        if (data.introduce) { this.introduce = data.introduce }
        if (data.logo) { this.logo = this.rootImgUrl + data.logo }
        this.wxShare({
          title: `邀您参加【${this.name}】线上路演活动`,
          content: '亲，打开了解活动详情',
          imgUrl: this.logo
        })
      }).catch((error) => {
        console.error(error)
      })
    },
    getAndroidApkUrl () {
      MiniSiteApi.getApkUrl().then(data => {
        this.apkUrl = (data && data.url) ? data.url : ''
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin"
  .share-online-activity
    background: #ffffff
    .page-panel
      display: flex;
      align-items: center;
      justify-content: center;
    .guide-open-browser
      position: fixed
      left: 0
      top: 0
      right: 0
      bottom: 0
      z-index: 1000
      opacity: 0.7
      background: #000000
      .guide-img
        width: 5.12rem
        height: 2.54rem
        margin: 0.48rem auto 0
        bg-image('./bg_guide')
        background-size: 100% 100%
        background-repeat: no-repeat
    .container
      .wrapper
        position: relative
        width: 6.38rem
        height: 10.68rem
        bg-image('./share_bg')
        background-size: 100% 100%
        .m-ul
          padding-top: 3.1rem
          .m-li
            position: relative
            padding: 0 0.8rem 0 2.4rem
            font-family: PingFangSC-Medium;
            font-size: 0.32rem;
            color: #FFFFFF;
            line-height: 0.44rem
            min-height: 0.44rem
            &.m-li-name
              padding-bottom: 0.5rem
            .title
              position: absolute
              left: 0.8rem
              top: 0
            .desc-intro
              font-size: 0.28rem
              line-height: 0.46rem
              display:-webkit-box;
              overflow : hidden;
              text-overflow: ellipsis;
              -webkit-line-clamp: 10;
              -webkit-box-orient: vertical;
        .join-btn
          position: absolute
          bottom: 0.3rem
          left: 1.65rem
          width: 3.1rem
          height: 0.8rem
          line-height: 0.8rem
          border-radius: 0.4rem
          text-align: center
          font-family: PingFangSC-Regular;
          font-size: 0.3rem
          color: #41403E;
          background-color: #FFC134
</style>


